import React, {useEffect, useState} from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";

import { Line, Button } from "components";
import TechStackList from "components/TechStackList";
import  ButtonNew  from 'components/ButtonNew';
import { SET_BACKEND_DATABASE } from "store/slices/backend.slice";
import { makeApiRequest } from "services/Api/api";
import cloneDeep from 'lodash/cloneDeep';

const DatabasePage = () => {
  const [databaseList,setDatabaseList] = useState([]);
  const selected_database = useSelector(store => store.backend.backend_details?.database);
  const selected_backendTech = useSelector(store => store.backend.backend_details?.backend);
  const navigate = useNavigate();
  const [selectedTech, setSelectedTech]  = useState(selected_database);
  const dispatch = useDispatch();
  console.log(selected_backendTech)
  console.log(selected_database)
  useEffect(() =>{
    setSelectedTech(selected_database);
  },[selected_database])

  useEffect(() => {
    (async (_) => {
      const DbListResponse = await makeApiRequest(
        "GET",
        `/BackendTemplates?$filter=BackendTemplateMainTechnology eq ${selected_backendTech?.BeTechId}`
      );
      console.log(DbListResponse.value);
      let screenList = cloneDeep(DbListResponse.value);
      const extractedList = await Promise.all(
        screenList.map(async (item) => {
          if (item?.BackendTemplateSupported_Database != null) {
            const databaseObj = await makeApiRequest(
              "GET",
              `/Databases(${item?.BackendTemplateSupported_Database})`
            );
            console.log(databaseObj);
            return { ...databaseObj, TemplateId: item?.TemplateId };
          }
        })
      );
      console.log(extractedList);
      setDatabaseList(extractedList);
    })();
  }, []);
  const techList = [
    { name: "MySQL", icon: "images/img_reply.svg" , 
      description1: `Pharetra morbi libero id aliquam elit massa integer
      tellus. Quis felis aliquam ullamcorper porttitor. Pulvinar
      ullamcorper sit dictumst ut eget a, elementum eu. Maecenas
      est morbi mattis id in ac pellentesque ac.`, 
      description2: `Lectus id duis vitae porttitor enim gravida morbi.
      Eu turpis posuere semper feugiat volutpat elit, ultrices
      suspendisse. Auctor vel in vitae placerat.
     
      Suspendisse maecenas ac donec scelerisque diam sed est
      duis purus.
    
      Elit nisi in eleifend sed nisi. Pulvinar at orci, proin
      imperdiet commodo consectetur convallis risus. Sed
      condimentum enim dignissim adipiscing faucibus consequat,
      urna. Viverra purus et erat auctor aliquam.`, 
      description3: `Lectus id duis vitae porttitor enim gravida morbi.
      Eu turpis posuere semper feugiat volutpat elit, ultrices
      suspendisse. Auctor vel in vitae placerat.
      Suspendisse maecenas ac donec scelerisque diam sed est
      duis purus.`},
      { name: "Open UI5", icon: "images/img_group.png" , 
      description1: `Open UI5 morbi libero id aliquam elit massa integer
      tellus. Quis felis aliquam ullamcorper porttitor. Pulvinar
      ullamcorper sit dictumst ut eget a, elementum eu. Maecenas
      est morbi mattis id in ac pellentesque ac.`, description2: `Open UI5 morbi libero id aliquam elit massa integer
      tellus. Quis felis aliquam ullamcorper porttitor. Pulvinar
      ullamcorper sit dictumst ut eget a, elementum eu. Maecenas
      est morbi mattis id in ac pellentesque ac.`},
    { name: "PostgreSQL", icon: "images/img_location_black_900_90x90.svg", description1: 'PostgreSQL description' },
    { name: "JDBC", icon: "images/img_fire.svg", description1: 'JDBC description', locked: true },
    { name: "MongoDB", icon: "images/img_location_light_green_601.svg", description1: 'MongoDB description' },
    { name: "DynamoDB",  icon: "images/img_user_cyan_901.svg", description1: 'DynamoDB description' }
  ];
  const updateTech = (val) => {
    setSelectedTech(val);
  }
  const onSubmitHandler = () => {      
       dispatch(SET_BACKEND_DATABASE(selectedTech));
       navigate("/backend-select-capabilities");
  }
  return (
    <>
          <div className="flex flex-1 flex-col gap-[32px] items-center justify-start md:mt-[0] mt-[32px] md:px-[20px] w-[100%]">
            <div className="flex flex-col items-center justify-start w-[100%]">
              <TechStackList choosen="Database" techList={databaseList} updateTech={updateTech} selectedTech={selectedTech} techType="database"
                title="Select Database" 
                description="Choose the database you want your project to build in"
                rhsDescription = "Select a Database to see it’s Features" />
            </div>
            <div className="bg-gray_900 flex flex-col gap-[16px] md:h-[auto] items-start justify-start max-w-[982px] w-[100%]">
              <Line className="bg-gray_801 h-[1px] w-[100%]" />
              <div className="flex items-start justify-start w-[100%]">
                <div className="flex flex-row gap-[12px] items-center justify-end w-[100%]">
                <ButtonNew
                   onClick={() => navigate(-1)}
                   children="Previous"
                  >
                  </ButtonNew>
                  <ButtonNew
                    isActive={Object.keys(selectedTech).length > 0}
                    onClick={() =>
                      Object.keys(selectedTech).length > 0 ?
                     onSubmitHandler() : null}
                    children="Next"
                    isSubmit
                  >
                  </ButtonNew>
                </div>
              </div>
            </div>
          </div>
    </>
  );
};

export default DatabasePage;
